#Jumbotron {
    --width: 30vw;
    --rotate: 0deg;

    --fontSmall: 8px;
    --fontMedium: 12px;
    --fontLarge: 18px;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;

    width: 100%;
    height: 100%;
}

#container {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    -webkit-perspective: 1500px;

            perspective: 1500px;
    width: 100%;
    height: 100%;
}

.tab {
    position: absolute;

    left: calc(50% - (var(--width) / 2));
    top: calc(50% - 460px/2);
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;

    width: var(--width);

    -webkit-box-shadow: 2px 2px 10px black;

            box-shadow: 2px 2px 10px black;
    -webkit-transition: 500ms;
    -o-transition: 500ms;
    transition: 500ms;
    color: white;
    opacity: 0.90;
}

.tab:hover{
    opacity: 1.0;
}

.rotate {
    position: absolute;
    opacity: .7;
    top: calc(50% - 35px);
    background-color: #FFF0;
    height: 70px;
    width: 70px;
    color: black;
    border: none;

    -webkit-transform: translateZ(var(--width)) rotate(45deg);

            transform: translateZ(var(--width)) rotate(45deg);
    z-index: 20;
    -webkit-transition: 300ms;
    -o-transition: 300ms;
    transition: 300ms;
}

.rotate:hover{
    cursor: pointer;
    -webkit-filter: brightness(1.2);
            filter: brightness(1.2);
    opacity: 1.0;
}

.rotate:focus{
    outline: none;
}

    .left-rot {
        left: calc(50% - var(--width));
        -webkit-box-shadow:
        15px -15px 0px inset white;
                box-shadow:
        15px -15px 0px inset white;
    }

    .right-rot {
        right: calc(50% - var(--width));

        -webkit-box-shadow:
        -15px 15px 0px inset white;

                box-shadow:
        -15px 15px 0px inset white;
    }



